import axios from 'axios';
import React, { Component } from 'react';
import Movie from './components/Movie';

import './App.css';

class App extends Component {
  state = {
    isLoading: true,
    movies: [],
  };
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      'https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating'
    );
    // console.log(movies);

    this.setState({
      isLoading: false,
      movies: movies,
    });
  };

  componentDidMount() {
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      <>
        <div className='App'>
          {isLoading
            ? 'Loading'
            : movies.map((movie) => (
                <Movie
                  key={movie.id}
                  title={movie.title}
                  year={movie.year}
                  genres={movie.genres}
                  poster={movie.medium_cover_image}
                  summary={movie.summary}
                />
              ))}
        </div>
      </>
    );
  }
}

export default App;
